*/

.sub-heading{
  font-size: 2.75rem;
  text-align: left;
  margin-left: 3rem;
  margin-right: 3rem;
  margin-top: 3rem;
  margin-bottom: 0rem;
}

h3{
  text-align: left;
  margin-left: 3rem;
  margin-right: 3rem;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

@media only screen and (max-width: 786px){
  .sub-heading{
    font-size: 1.5rem;
    text-align: left;

    margin: 0rem 2.2rem;
  }

  .homepage-grid{
    margin: 0.5rem 0rem;
  }
}